.feed {
    display: flex;
    flex-direction: column;
    flex: 0.65;
    border-right: 1px solid var(--listener-grey);
    min-width: fit-content;
    overflow-y: scroll;
}

/* Hide scrollbar for Chrome, Safari, and Opera */
.feed::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar for IE, Edge, and Firefox */
.feed {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}

.feed-header {
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    border: 1px solid var(--listener-grey);
    padding: 15px 20px;
}

.feed-header > h2 {
    font-size: 20px;
    font-weight: 900;
    margin-left: 0;
    text-align: center;
}

.no-posts {
    text-align: center;
    font-weight: 400;
}

.posts {
    display: flex;
    flex-direction: column;
}

.post {
    display: flex;
    flex-direction: column;
    width: 100%;
}